import React, { useEffect, useState } from 'react'
import axios from 'axios'
import PdfViewer from '../../../components/ExibirApostila'
import { Video } from '../../../components/Video'
import { Header } from '../../../components/Header'
import BotaoGrupos from '../../../components/BotaoGrupos'
import { Botao, BotaoIrSumario } from '../../../components/Botao/styles'
import { SmallButton } from '../../Login/styles'
import { cores } from '../../../styles'
import { ExibirMensagem } from './styles'

export const L1: React.FC = () => {
  const [tokenPresente, setTokenPresente] = useState(false) // Estado para controlar se o token est presente
  const [mensagem, setMensagem] = useState('') // Estado para armazenar a mensagem

  useEffect(() => {
    const verificarToken = () => {
      const token = localStorage.getItem('token')
      if (token) {
        // Se o token estiver presente, definir tokenPresente como verdadeiro
        setTokenPresente(true)
      } else {
        // Se o token no estiver presente, definir tokenPresente como falso e definir a mensagem
        setTokenPresente(false)
        setMensagem(
          'Faa a assinatura para visualizar a apostila e os exerccios da lio'
        )
      }
    }

    verificarToken()
  }, [])

  const customContent = () => (
    <BotaoIrSumario to="/Sumario">Lies</BotaoIrSumario>
  )

  return (
    <div className="container">
      <Header customContent={customContent} />
      <div>
        <Video />
        {tokenPresente ? (
          <>
            <PdfViewer />
            <BotaoGrupos />
          </> // Renderizar PdfViewer se o token estiver presente
        ) : (
          <ExibirMensagem>
            <p>{mensagem}</p>

            <Botao to="/Login" color={cores.laranja}>
              ENTRAR
            </Botao>
          </ExibirMensagem>
          // Exibir mensagem se o token no estiver presente
        )}
      </div>
    </div>
  )
}

export default L1
