import React, { useState, useEffect } from 'react'
import { Container } from 'react-bootstrap'
import {
  BarraFalha,
  BarraSucesso,
  BotaoIrAssinatura,
  DivAssinatura
} from './styles'
import { Botao, BotaoClaro } from '../../components/Botao/styles'
import { BotaoPerfil } from '../Perfil/styles'

const Assinatura = () => {
  const [assinaturaCompleta, setAssinaturaCompleta] = useState(null) // Alterado para null para indicar que a informao ainda no foi carregada

  useEffect(() => {
    const token = localStorage.getItem('token')

    fetch('/user/verifica-assinatura', {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${token}`
      }
    })
      .then((response) => response.json())
      .then((data) => {
        // Definindo o estado de acordo com o valor de user.assinatura
        setAssinaturaCompleta(data.assinatura)
      })
      .catch((error) => {
        console.error('Erro ao verificar status da assinatura:', error)
        // Trate o erro conforme necessrio
      })
  }, [])

  // Verifica se a informao j foi carregada
  if (assinaturaCompleta === null) {
    return <div>Carregando...</div>
  }

  // Renderiza de acordo com o valor de assinaturaCompleta
  return (
    <Container>
      {assinaturaCompleta ? (
        <>
          {' '}
          <DivAssinatura>
            <BarraSucesso>Assinatura realizada com sucesso!</BarraSucesso>
            <ul>
              <li>Acesso liberado s apostilas!</li>
              <li>
                Acesso liberado  coletnea de exerccios organizados por nvel
                de dificuldade!
              </li>
              <li>Acesso liberado s resolues de exerccios passo-a-passo</li>
            </ul>
            <BotaoIrAssinatura to="/Sumario">
              {' '}
              Ir para lista de lies
            </BotaoIrAssinatura>
          </DivAssinatura>
        </>
      ) : (
        <>
          <BarraFalha>Ops... algo deu errado</BarraFalha>
          <DivAssinatura>
            <p>
              <br />
              No foi possvel completar a assinatura. Revise seus mtodos de
              pagamento.
            </p>
            <BotaoIrAssinatura to="/user/perfil">
              {' '}
              Ir para pagamento
            </BotaoIrAssinatura>
          </DivAssinatura>
        </>
      )}
    </Container>
  )
}

export default Assinatura
