import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { Cabecalho, HeaderSumario } from '../../components/Header/styles'
import { Header } from '../../components/Header'
import { SmallButton } from '../Login/styles'
import FormPerfil from '../../components/Formulario_perfil'
import { ButtonToolbar, Container } from 'react-bootstrap'
import { BotaoPerfil, HeaderPerfil } from './styles'
import { Button } from '../../components/Enunciado/styles'
import FormularioCadastro from './FormularioCadastro'
import { FormularioPgto } from '../../components/Formulario_perfil/styles'
import CardForm from './Pagamento'

const Perfil = () => {
  const [userInfo, setUserInfo] = useState(null)

  useEffect(() => {
    const fetchUserProfile = async () => {
      try {
        const token = localStorage.getItem('token')
        if (!token) {
          // Se o token no estiver presente, redirecionar o usurio para a pgina de login
          window.location.replace('/login')
          return
        }

        // Fazer uma requisio para o backend para obter as informaes do usurio
        const response = await axios.get('/user/perfil', {
          headers: {
            Authorization: `Bearer ${token}` // Enviar o token armazenado no localStorage no cabealho da requisio
          }
        })

        setUserInfo(response.data)
        console.log(response.data)
      } catch (error) {
        // Se houver erro ao obter as informaes do usurio, tratar o erro aqui
        console.error('Erro ao obter as informaes do usurio:', error)
      }
    }

    fetchUserProfile()
  }, [])

  return (
    <div>
      <Container>
        <HeaderPerfil>
          <BotaoPerfil to="/Sumario"> Ir para Sumrio</BotaoPerfil>
        </HeaderPerfil>
        <h2>Informaes do Usurio</h2>
        {userInfo ? (
          <div>
            <p>Nome: {userInfo.nome}</p>
            <p>Email: {userInfo.email}</p>
          </div>
        ) : (
          <>
            <p>Carregando informaes do perfil...</p>
          </>
        )}

        {/* Renderizao condicional dos componentes com base na assinatura */}
        {userInfo && userInfo.assinatura === 'true' ? (
          <>
            {/* Aqui voc pode adicionar os componentes que deseja renderizar quando a assinatura for verdadeira */}
            {/* Por exemplo, voc pode adicionar um texto ou outro contedo */}
            <p>O usurio possui uma assinatura ativa.</p>
          </>
        ) : (
          <>
            {/* Renderizar os componentes FormularioCadastro e CardForm apenas se a assinatura for diferente de "true" */}
            <FormularioCadastro userInfo={userInfo} />
            <CardForm userToken={localStorage.getItem('token')} />
          </>
        )}
      </Container>
    </div>
  )
}

export default Perfil
