import React, { useState } from 'react' // Adicione o import de useState aqui

import { useFormik } from 'formik'
import * as Yup from 'yup'
import { BtnEnviar, NavLogin, SmallButton, StyledInput } from '../Login/styles'
import { cores } from '../../styles'
import { Container } from '@mui/material'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'

const CadastroForm = () => {
  const navigate = useNavigate() // Cria a instncia de useNavigate

  const [error, setError] = useState(null)

  const formik = useFormik({
    initialValues: {
      nome: '',
      email: '',
      senha: '',
      confirmaSenha: ''
    },
    validationSchema: Yup.object({
      nome: Yup.string().required('Campo obrigatrio'),
      email: Yup.string()
        .email('E-mail invlido')
        .required('Campo obrigatrio'),

      senha: Yup.string().required('Campo obrigatrio'),
      confirmaSenha: Yup.string()
        .oneOf([Yup.ref('senha')], 'As senhas no coincidem')
        .required('Campo obrigatrio')
    }),
    onSubmit: async (values) => {
      try {
        const response = await axios.post('/auth/register', values)

        // Extrair o token da resposta
        const token = response.data.token

        // Salvar o token localmente (por exemplo, em localStorage)
        localStorage.setItem('token', token)

        console.log('Usurio cadastrado com sucesso:', response.data)

        // Redirecionar para "/user/perfil" aps salvar o token
        navigate('/user/perfil')

        // Redirecionar para pgina de sucesso, fazer login automtico, etc.
      } catch (error) {
        console.error('Erro ao cadastrar usurio:', error)
      }
    }
  })
  return (
    <Container>
      <NavLogin>
        <SmallButton to="/Sumario" color={cores.laranjaClaro}>
          LIES
        </SmallButton>
      </NavLogin>
      <form onSubmit={formik.handleSubmit}>
        <div>
          <label htmlFor="nome">Nome:</label>
          <StyledInput
            type="nome"
            id="nome"
            name="nome"
            value={formik.values.nome}
            onChange={formik.handleChange}
          />
          {formik.touched.nome && formik.errors.nome && (
            <p style={{ color: 'red' }}>{formik.errors.nome}</p>
          )}
        </div>
        <div>
          <label htmlFor="email">E-mail:</label>
          <StyledInput
            type="email"
            id="email"
            name="email"
            value={formik.values.email}
            onChange={formik.handleChange}
          />
          {formik.touched.email && formik.errors.email && (
            <p style={{ color: 'red' }}>{formik.errors.email}</p>
          )}
        </div>

        <div>
          <label htmlFor="senha">Senha:</label>
          <StyledInput
            type="password"
            id="senha"
            name="senha"
            value={formik.values.senha}
            onChange={formik.handleChange}
          />
          {formik.touched.senha && formik.errors.senha && (
            <p style={{ color: 'red' }}>{formik.errors.senha}</p>
          )}
        </div>
        <div>
          <label htmlFor="confirmaSenha">Confirmao de Senha:</label>
          <StyledInput
            type="password"
            id="confirmaSenha"
            name="confirmaSenha"
            value={formik.values.confirmaSenha}
            onChange={formik.handleChange}
          />
          {formik.touched.confirmaSenha && formik.errors.confirmaSenha && (
            <p style={{ color: 'red' }}>{formik.errors.confirmaSenha}</p>
          )}
        </div>
        <BtnEnviar type="submit">Cadastrar</BtnEnviar>
      </form>
    </Container>
  )
}

export default CadastroForm
